import * as React from 'react'
import { Link, graphql } from 'gatsby'
import { GatsbyImage, getImage } from 'gatsby-plugin-image'
import Layout from '../components/layout'
import Card from '../components/card'

const BlogHeroCard = ({ slug, frontmatter, excerpt }) => {
  const heroImage = frontmatter.heroImage
  const image = getImage(heroImage.file)
  const alt = heroImage.alt || ""

  return (
    <Card>
      <Link to={`posts/${slug}`}>
        <div className="p-2 space-y-4">
          <div>
            <GatsbyImage className="h-48 rounded-t-md" image={image} alt={alt}/>
            <p className="text-xl text-teal-600">{frontmatter.title}</p>
            <p className="text-xs text-zinc-400">{frontmatter.date}</p>
          </div>
          <div className="text-sm">
            {excerpt}
          </div>
        </div>
      </Link>
    </Card>
  )
}

const BlogCard = ({ slug, frontmatter, excerpt }) => {
  const heroImage = frontmatter.heroImage
  const image = getImage(heroImage.file)
  const alt = heroImage.alt || ""

  return (
    <Card>
      <Link to={`posts/${slug}`}>
        <div className="p-2 grid grid-cols-3 space-x-3">
          <div className="col-span-1">
            <GatsbyImage className="h-28 rounded-l-md" image={image} alt={alt}/>
          </div>
          <div className="space-y-4 col-span-2">
            <div>
              <p className="text-lg text-teal-600">{frontmatter.title}</p>
              <p className="text-xs text-zinc-400">{frontmatter.date}</p>
            </div>
            <p className="text-xs line-clamp-3">{excerpt}</p>
          </div>
        </div>
      </Link>
    </Card>
  )
}

const BlogPage = ({ data }) => {
  const pages = data.allFile.nodes.map(node => node.childMdx);
  const latest = pages.shift();
  return (
    <Layout>
      <BlogHeroCard {...latest}/>
      {
        pages.map(node => (
          <BlogCard key={node.id} {...node} />
        ))
      }
    </Layout>
  )
}

export const query = graphql`
  query {
    allFile(
      sort: {fields: childMdx___frontmatter___date, order: DESC}
      filter: {sourceInstanceName: {eq: "blog"}, extension: {eq: "mdx"}}
    ) {
      nodes {
        childMdx {
          frontmatter {
            date(formatString: "MMMM D, YYYY")
            title
            heroImage {
              file {
                childImageSharp {
                  gatsbyImageData
                }
              }
            }
          }
          id
          slug
          excerpt(pruneLength: 300)
        }
      }
    }
  }
`

export default BlogPage
